import React, { useContext, useEffect, useState } from "react";
import { ethers } from "ethers";
import { useAccount } from "wagmi";
import { useWeb3Modal } from "@web3modal/react";
import { useBalanceContext } from "~CONTEXT/pool-data";
import "./liquidity_market.sass";


const Liquidity_market: React.FC = () => {
  const { address, connector } = useAccount();
  const { open } = useWeb3Modal();
  // const { data } = useContext(useBalanceContext);
  const [sortField, setSortField] = useState<"name" | "balance">("name");
  const [sortDirection, setSortDirection] = useState<"asc" | "desc">("asc");
  const { tokenAddresses, setTokenAddresses, balances, setBalances } = useBalanceContext();


  const sortedData = [...balances].sort((a, b) => {
    const fieldA = a[sortField];
    const fieldB = b[sortField];
    let comparison = 0;
    if (fieldA > fieldB) {
      comparison = 1;
    } else if (fieldA < fieldB) {
      comparison = -1;
    }
    // if sorting by balance, sort direction is reversed
    return sortField === 'balance' ? comparison * -1 : comparison;
  });

  return (
    <div className="Liquidity_market">
      <div>
        <button onClick={() => { setSortField("name"); setSortDirection(sortDirection === "asc" ? "desc" : "asc"); }}>Name</button>
        <button></button>
        <button onClick={() => { setSortField("balance"); setSortDirection(sortDirection === "asc" ? "desc" : "asc"); }}>Balance</button>
      </div>
      <div>
        {sortedData.map((balance, index) => (
          <div key={index}>
            <div>
              <h2>{balance.name}</h2>
            </div>
            <div>
              <p>{tokenAddresses[index].slice(0,6)}...{tokenAddresses[index].slice(-4)}</p>
            </div>
            <div>
              <p>{balance.balance}</p>
            </div>
          </div>
        ))}
      </div>
    </div>
  );
};

export default Liquidity_market;
